.current {
    color: lightgray;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}

a {
    color: lightblue;   
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
}

body {
    background-color: #222244;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 20%;
    height: auto;
    border: 10px solid #113;
}

@media only screen and (max-width: 1500px){
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 30%;
    border: 10px solid #113;
  }
}

@media only screen and (max-width: 1200px){
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 40%;
    border: 10px solid #113;
  }
}

@media only screen and (max-width: 700px){
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 60%;
    border: 10px solid #113;
  }
}

h1 {
    color: white;
    text-align: center;
}

h4 {
    text-align: center;
    color: lightgray;
    font-family: verdana;
    font-size: 20px;
    font-weight: normal;
}

p {
    text-align: center;
    color: lightgray;
    font-family: verdana;
    font-size: 20px;
}

nav a {
    margin-left: 20px;
    marign-right: 20px;
}

.bignav {
    margin-top: 1px;
    margin-bottom: 1px;
    text-align:center;
    font-size: 20px;
}

div {
    display: flex;
    flex-direction: column-reverse;
}
